@use 'global' as *;

.c-section-header {
  color: $blue;
  text-align: center;
}

.c-section-header__jatitle {
color: $blue;
font-size:  rem(13);
font-weight: bold;
letter-spacing: 0.05em;
line-height: 1;
font-family:$english-family;
@include mq(md) {
    font-size: rem(14);
}

}

.c-section-header__engtitle {
font-size:  rem(30);
font-weight: bold;
letter-spacing: 0.05em;
line-height: calc(37 /30);
font-family:$english-family;
text-transform: uppercase;
@include mq(md) {
    font-size: rem(36);
}
}